import React from 'react';
import Link from 'gatsby-link';
import volkswagenImg from "./static/images/volkswagenImg.jpg";
import penguinImg from "./static/images/penguinImg.jpg";
import vwDealershipImg from "./static/images/vwDealershipImg.jpg";
import boscoImg from "./static/images/boscoImg.jpg";
import visacompanyImg from "./static/images/visacompanyImg.jpg";
import supersonicImg from "./static/images/supersonicImg.jpg";
import volkswagenEmailerImg from "./static/images/volkswagenEmailerImg.jpg";
import ebayImg from "./static/images/ebayImg.jpg";
import radioShowImg from "./static/images/radioShowImg.jpg";

const IndexPage = () => (
  <section>
    <article className="intro">
      <div className="text">I’m a Graphic Designer<br />
        Based in Mumbai, India</div>
      <div className='scrollDown'></div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/vwtiguan">
            <img src={volkswagenImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>VOLKSWAGEN TIGUAN<br />
              APP DESIGN</h2>
            <p>Designed for Volkswagen India for the launch of Tiguan.</p>
            <Link to="/vwtiguan" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/penguin">
            <img src={penguinImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>PENGUIN INDIA<br />
              WEBSITE REDESIGN</h2>
            <p>Website design for Penguin Publication India. </p>
            <Link to="/penguin" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/vwdealership">
            <img src={vwDealershipImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>VOLKSWAGEN<br />
              Dealership Website</h2>
            <p>Website design for Volkswagen India Dealership.</p>
            <Link to="/vwdealership" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/boscojack">
            <img src={boscoImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>BOSCO JACK<br />
              BRANDING</h2>
            <p>Logo and Stationery design for DJ and Entrepreneur Bosco Fernandes.</p>
            <Link to="/boscojack" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/visacompany">
            <img src={visacompanyImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>THE VISA COMPANY<br />
              BRANDING</h2>
            <p>Logo Design for Travel agency.</p>
            <Link to="/visacompany" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/vh1supersonic">
            <img src={supersonicImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>Vh1 SUPERSONIC<br />
              SOCIAL MEDIA</h2>
            <p>Social creatives for Music Festival Vh1 Supersonic.</p>
            <Link to="/vh1supersonic" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/vwemailers">
            <img src={volkswagenEmailerImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>VOLKSWAGEN INDIA<br />
              EMAILERS</h2>
            <p>Emailers designed for various cars for Volkswagen India email marketing.</p>
            <Link to="/vwemailers" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/ebayindia">
            <img src={ebayImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>EBAY INDIA<br />
              EMAILERS</h2>
            <p>Emailers designed for Ebay India email marketing.</p>
            <Link to="/ebayindia" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="work">
      <div className="vMid">
        <div className="midWrapper">
          <div className="workImg">
            <Link to="/flyerdesign">
            <img src={radioShowImg} alt="" />
            </Link>
          </div>
          <div className="text">
            <h2>RADIO SHOW<br />
              FLYERS</h2>
            <p>Radio show flyers for psychedelic transcendence</p>
            <Link to="/flyerdesign" className="learnMore">
            <span>learn more</span>
            </Link>
          </div>
        </div>
      </div>
    </article>
    <article className="contact" id="contact">
      <div className="text">
        <h2>LET’S CONNECT!</h2>
        <p>I’m always interested in hearing about new Design projects. So if you would like to chat do get in touch. Here’s where you need to write : <br />
          <Link to="mailto:akashgohil93@gmail.com" target="_blank">
          akashgohil93@gmail.com
          </Link>
        </p>
      </div>
      <div className="midWrapper">
        <div className="socialLinks">
          <ul>
            <li><a href="https://www.behance.net/aakashgohil" target="_blank">BEHANCE</a></li>
            <li><a href="https://www.linkedin.com/in/aakash-gohil-9320617a/" target="_blank">LINKEDIN</a></li>
            <li><a href="https://www.instagram.com/aakash.gohil.52/" target="_blank">INSTAGRAM</a></li>
          </ul>
        </div>
      </div>
    </article>
    <div className="bottomBox"></div>
  </section>
)

export default IndexPage



